.element__delete {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 18px;
    height: 19px;
    border: none;
    background-color: transparent;
    background-image: url("../../../images/delete-btn.svg");
    background-repeat: no-repeat;
    background-position: center;
    transition: opacity 0.3s;
}

.element__delete:hover {
    cursor: pointer;
    opacity: 0.6;
}
